import baffle from 'baffle';
import delay from 'lodash.delay';

const MAIN_TITLE_BAFFLE_DELAY = 2000;
const KEYUP_TRESHOLD = 1000;
const CYBER_KEYCODES = [67, 89, 66, 69, 82];

let keyCodeBuffer = [];
let keyupTimeout = null;

function clearKeyCodeBuffer() {
  keyCodeBuffer = [];
}

function onKeyup(event) {
  keyCodeBuffer.push(event.keyCode);

  if(keyCodeBuffer.join(',') === CYBER_KEYCODES.join(',')) {
    doBaffle();
    clearKeyCodeBuffer();
  }

  if(keyupTimeout) {
    clearTimeout(keyupTimeout);
  }

  keyupTimeout = setTimeout(() => {
    clearKeyCodeBuffer();
  }, KEYUP_TRESHOLD);
}

function doBaffle() {
  const baffledMainTitle = baffle(document.querySelector('.main-header-title-wrapper__title .baffled-title')).start();

  delay(() => baffledMainTitle.reveal(5000), MAIN_TITLE_BAFFLE_DELAY);
}

function initializeHeader() {
  $(document).on('keyup', onKeyup);
}

export default initializeHeader;



